body{
    background: rgb(233, 233, 233);
} 
.btn{
    color:white;
    background:#3481A2;
    padding: 5px 10px;
    font-size: 14px;
    font-weight: 500;
    border:1px solid white;
    outline:none;
    border-radius:4px;
    cursor: pointer;
    transition: 0.3s all;
    &.active{
        background:white;
        color: #3481A2;
    }
}
.wrapper{
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-wrap: wrap;
}
.chat-box__header{
    height: 40px;
    .row{
        height: inherit;
    }
}
.chat-box__wrapper{
    min-height:100vh;
    height: 100vh;
    padding: 10px 0;
}
.chat-box{
    box-shadow: rgba(0, 0, 0, 0.19) 0px 1px 4px 0px, rgba(0, 0, 0, 0.23) 0px 1px 4px 0px;
    max-width: 600px;
    width: 600px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.chat-box__header{
    padding: 0 15px;
    display: flex;
    background-color: rgb(52, 129, 162);
    justify-content: space-between;
    align-items: center;
    font-family: 'Roboto', sans-serif;
    box-shadow: rgba(0, 0, 0, 0.2) 0px 1.5px 4px 0px;
    font-weight: 300;
}
.chat-box__content{
    flex: 1;
    background: rgb(248, 248, 248);
    padding: 15px 0;
    overflow-y: auto;
    overflow-x: hidden;
}
.chat-box__footer{
    min-height: 40px;
    background: white;
}
.header__text{
    color: white;
    font-size: 18px;
    cursor: none;
    pointer-events: none;
}
.container-fluid{
    padding: 0;
}
.header__row{
    height: 40px;
}
.text__uppercase{
    text-transform: uppercase;
}
.material-icon{
    font-family: "Material Icons";
    font-weight: normal;
    font-style: normal;
    letter-spacing: normal;
    text-transform: none;
    font-feature-settings: "liga";
    -webkit-font-smoothing: antialiased;
    user-select: none;
    font-variant: normal;
}
.controls{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    min-height:inherit;
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.2) 0px -1px 10px 0px;
    i{
        font-size: 21px;
        color :$NoHoverColor;
        transition: 0.3s all;
        &:hover{
            color:$blackColor;
        }
    }
    >div{
        margin: 0 10px;
        
        &:hover{
            cursor: pointer;
        }
    }
    .emojionearea{
        height: 30px;
        border:none;
        box-shadow: none;
        border-radius:0;
        border-bottom: 1px solid rgb(175, 175, 175);
        max-width: 500px;
        &.focused{
            border-color:rgba($blackColor,0.6);
        }
        .emojionearea-editor{
            font-family: 'Roboto', sans-serif;
            max-height: 30px;
            min-height: 30px;
            overflow: hidden;
            >br{
                display: none;
            }
            >div{
                display:none;
            }
        }
        &.emojionearea-inline{
            >.emojionearea-editor{
                height: 30px;
                font-family: 'Roboto', sans-serif;
                display: flex;
                justify-content: flex-start;
                padding: 0;
                align-items:center;

            }
        }
    }
    .emojionearea.focused{
        border-color:rgba($blackColor,0.6);
        box-shadow: none;
    }
    /*
    .emojionearea-editor{
        flex: 1;
        border-bottom: 1px solid rgb(175, 175, 175);
        padding: 5px 5px 5px 10px;
        font-family: 'Roboto',sans-serif;
        transition: 0.3s all;
        &.error{
            border-bottom-color: red;
        }
        &:hover,&:focus{
            border-bottom-color: rgba($blackColor,0.6);
        }
    } */
}
.message-list{
    padding:15px 15px;
}
.message-block{
    display: flex;
    align-items: center;
    font-family: 'Roboto',sans-serif;
    font-weight: 400;
    margin-bottom: 15px;
    font-size: 12px;
    padding:0 15px;
    .message-block__icon{
        .fa{
            font-size: 25px;
        }
    }
    .message-block__text{
        max-width:80%;
        br{
            display: none;
        }
        p{
            max-width: 100%;
            word-wrap: break-word;
            display: flex;
            align-items:center;
        }
    }
    &.self{
        justify-content: flex-start;
        flex-direction: row-reverse;
        .message-block__time{
            margin-right: 5px;
            margin-left: 0;
        }
        .message-block__text{
            img{
                height: 20px;
                margin-left:3px;
            }
        }
    }
    &.someone{
        .message-block__icon{
            .fa{
                font-size: 25px;
                color:$FromMessageColor;
            }
        }
        .message-block__text{
            background: $FromMessageColor;
            &:before{
                color: white;
                z-index: 20;
            }
        }
    }
    &.unread{
        background:rgba(0,0,0,0.12);
    }
    &.system{
        display:flex;
        justify-content:center;
        .message-block__text{
            background:none;
            box-shadow:none;
            border:none;
            text-transform:uppercase;
            font-size:14px;
            font-weight:400;
            color:rgb(115,115,115);
            &:before{
                display:none;
            }
        }
        .message-block__icon{
            .fa{
                font-size: 20px;
            }
        }
    }
}
.message-block__time{
    color: grey;
    font-size: 12px;
    margin-left: 5px;
}
.message-block__text{
    display: flex;
    align-items:center;
    padding: 9px 15px;
    margin:4px 0;
    box-shadow: rgba(0, 0, 0, 0.15) 0px 1px 1px 0px;
    background: #DEE9F0;
    border:1px solid rgb(222,222,222);
    border-radius: 5px;
    position: relative;
    font-size: 14px;
}
.emoji_list{
    span{
        display: inline-block;
        margin-bottom: 5px;
    }
}
.custom_block{
    height: 100vh;
    width: 250px;
    background-color: grey;
    &.hidden{
        background: transparent;
    }
}
.change-background{
    padding: 15px;
    padding-top: 10px;
}
.change-background__button{
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    color: white;
    background:$main-color;
    padding: 10px 25px;
    border-radius:4px;
    transition: .3s all;
    cursor: pointer;
    border:1px solid transparent;
    &.active{
        color:$main-color;
        background:white;
        border-color:#808080;
    }
}
.background-list{
    padding: 10px;
    background:white;
    display: none;
}
.background-list__item{
    margin-bottom: 10px;
    &:last-child{
        margin-bottom: 0;
    }
}
.background-list__link{
    display: flex;
    justify-content: flex-start;
    align-items:center;
    font-family: 'Roboto',sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: black;
}
.background-list__img{
    height: 50px;
    width: 50px;
    margin-right: 10px;
}
@media all and (max-width:1100px){
    .chat-box__wrapper{
        padding: 0;
    }
    .custom_block{
        &.hidden{
            display: none;
        }
    }
    .chat-box{
        width: 650px;
        max-width: 650px;
    }
    .controls{
        .emojionearea{
            max-width: 800px;
        }
    }
}
@media all and (max-width:900px){
    .chat-box__wrapper{
        width: 100%;
    }
    .custom_block{
        display: none;
    }
    .chat-box{
        width: 100%;
        max-width: 100%;
    }
}
@media all and (max-width:768px){
    .controls{
        .emojionearea{
            max-width: 85%;
        }
    }
}
@media all and (max-width:768px){
    .controls{
        .emojionearea{
            max-width: 85%;
        }
    }
}
@media all and (max-width:625px){
    .controls{
        .emojionearea{
            max-width: 83%;
        }
    }
}
@media all and (max-width:515px){
    .controls{
        .emojionearea{
            max-width: 79%;
        }
    }
}
@media all and (max-width:450px){
    .controls{
        .emojionearea{
            max-width: 75%;
        }
    }
}
@media all and (max-width:425px){
    html{
        position: relative;
    }
    .chat-box{
        position:absolute; top:0; bottom:0; right:0; left:0;
    }
    .chat-box__wrapper{
        min-height: unset;
        height: unset;
    }
}
@media all and (max-width:325px){
    .controls{
        .emojionearea{
            max-width: 70%;
        }
    }
}